.profile-page{
    margin: 20px 16px;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: -0.3px;
    color: #414141;
    figure{
        margin-bottom: 32px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        margin-top: 0;
        img{
            object-fit: cover;
            width: 64px;
            height: 64px;
            border-radius: 50%;
        }
    }
    .button-item{
        padding: 13px 16px;
        background: #F2F2F2;
        border-radius: 10px;
        text-decoration: none;
        display: flex;
        justify-content: space-between;
        color: #414141;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        margin-bottom: 10px;
        div{
            img{
                padding-right: 10px;
            }
        }
    }
}